@import url(https://cdnjs.cloudflare.com/ajax/libs/octicons/3.5.0/octicons.min.css);
html {
  height: 100%;
  display: flex;
}

body {
  margin: auto;
  background-image: -webkit-linear-gradient(45deg,
      rgba(134, 2, 232, 1) 42%,
      rgba(3, 62, 255, 0.9));
  background-image: linear-gradient(45deg,
      rgba(134, 2, 232, 1) 42%,
      rgba(3, 62, 255, 0.9));
}

.main-container {
  height: 50vh;
  width: 50vw;
  background-color: white;
  border-radius: 5px;
}
.container {
  background-color: white;
  border-radius: 5px;
  padding: 15px;
  text-align: center;
  /* min-width: 45vw; */
}

input {
  max-width: 55px;
}
nav {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  background-color: #0F0E1A;
}

nav>div {
  display: inline-block
}

nav>div.nav-left-container a,
nav>div.nav-right-container a {
  color: white;
}

.nav-left-container {
  margin: 0 25px;
}

.nav-right-container {
  float: right;
  margin: 0 25px;

}

.nav-right-container>a {
  margin: 0 9px;
}
.feedbackizzle button {
  vertical-align: top;
  background-color: yellow;
}

.feedbackizzle {
  background-color: #7777e157;
  text-align: center;
  position: fixed;
  bottom: 0px;
  right: 27px;
  width: 46px;
  height: 29px;
  border: 1px #4d4df6 solid;
  border-bottom: none;
  border-radius: 2px 2px 0 0;
  -webkit-transition: height 0.4s, width 0.5s;
  transition: height 0.4s, width 0.5s;
  -webkit-transition-timing-function: ease-in;
          transition-timing-function: ease-in;
}

.feedbackizzle .feedbackizzle-style div {
  -webkit-transition: text-shadow 0.2s;
  transition: text-shadow 0.2s;
  -webkit-transition-timing-function: ease-in;
          transition-timing-function: ease-in;
}

.feedbackizzle.expand {
  background-color: #6e6ee0;
  color: white;
  width: 270px;
  height: 45px;
}

.feedbackizzle.done {
  width: auto;
  width: initial;
}

.feedbackizzle.expand.stage-2 {
  height: 180px;
}

.feedbackizzle.expand .feedback-choices,
.feedbackizzle.expand .feedback-choices div {
  display: inline-block;
}

.feedbackizzle.expand .thoughts,
.feedbackizzle.expand .feedback-choices div {
  padding: 3px 10px 0;
}

.feedbackizzle div.thoughts {
  z-index: 1;
  padding: 3px 10px 0 15px;
}

.feedbackizzle .thoughts:hover,
.feedbackizzle .thoughts:focus,
.feedbackizzle.expand .feedback-choices div:hover,
.feedbackizzle.expand .feedback-choices div:focus {
  text-shadow: rgba(0, 0, 0, 0.5) 2px 2px 4px;
}

.feedbackizzle .thoughts:active,
.feedbackizzle.expand .feedback-choices div:active {
  text-shadow: rgba(0, 0, 0, 0.9) 1px 1px 4px;
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}

.feedbackizzle .feedback-choices {
  display: none;
  font-size: 0;
}

.feedbackizzle .thoughts {
  display: inline-block;
  cursor: default;
  padding: 3px 10px;
  float: right;
}

.feedbackizzle .feedbackizzle-style .feedback-choices div {
  font-size: xx-large;
  cursor: pointer;
}

.feedbackizzle.expand .thoughts {
  font-size: medium;
  font-size: initial;
}

.feedbackizzle .noselect {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Chrome and Opera */
}

.hidden {
  display: none;
}
